<template>
  <v-app>
      <v-avatar color="teal" size="56" id="DragMe">Drag Me</v-avatar>
      <div style="border:1px solid red" id="div2">Drop Here</div><br>
  </v-app>
</template>

<script>
import requirejs from "requirejs"; // Import RequireJS

export default {
  mounted: function () {
    var me = this;
    requirejs(
      ["DS/DataDragAndDrop/DataDragAndDrop"],
      function (DataDragAndDrop) {
        me.createDragable(DataDragAndDrop, document.getElementById("DragMe"));
        me.createDropable(DataDragAndDrop, document.getElementById("div2"));
      }
    );
  },
  methods: {
    createDragable(DataDragAndDrop, elem) {
      DataDragAndDrop.draggable(elem, {
        data: JSON.stringify(elem.innerText),
      });
    },
    createDropable(DataDragAndDrop, elem) {
      DataDragAndDrop.droppable(elem, {
        drop: function (data) {
          elem.innerText = data;
        },
      });
    },
  },
};
</script>


<style></style>
